button.fill.teal:hover {
  background-color: transparent;
  color: var(--teal-400);
}
button.fill.blue:hover {
  background-color: transparent;
  color: var(--blue-400);
}
button.fill.red:hover {
  background-color: transparent;
  color: var(--red-400);
}
button.border.teal:hover {
  background-color: var(--teal-400);
  color: var(--white);
}
button.border.blue:hover {
  background-color: var(--blue-400);
  color: var(--white);
}
button.border.red:hover {
  background-color: var(--red-400);
  color: var(--white);
}
button.border.gray:hover {
  background-color: var(--gray-400);
  color: var(--white);
}

button.color.teal {
  color: var(--teal-300);
}
button.color.red {
  color: var(--red-300);
}
button.color.gray {
  color: var(--gray-300);
}
button.color.blue {
  color: var(--blue-300);
}
button.color.teal:hover {
  color: var(--teal-400);
}
button.color.red:hover {
  color: var(--red-400);
}
button.color.gray:hover {
  color: var(--gray-400);
}
button.color.blue:hover {
  color: var(--blue-400);
}